const AUTH_ENDPOINT = 'https://accounts.spotify.com/authorize';
const REDIRECT_URI = 'https://korsawat.github.io/Song/';
const CLIENT_ID = "e09c3389cd8f4a0c92f03e1bc5b423c2"; // Replace with your Spotify client ID
const SCOPES = [
  'user-library-read', 
];

export const loginUrl = `${AUTH_ENDPOINT}?client_id=${CLIENT_ID}&redirect_uri=${REDIRECT_URI}&scope=${SCOPES.join(
  '%20'
)}&response_type=token&show_dialog=true`;

export const getTokenFromUrl = () => {
  const hashParams = new URLSearchParams(window.location.hash.substring(1));
  const accessToken = hashParams.get('access_token');
  return accessToken;
};

export const getLikedSongs = async (token) => {
  const limit = 50; // Number of items to fetch per request
  let offset = 0;
  let allSongs = [];

  try {
    let data;

    do {
      const response = await fetch(`https://api.spotify.com/v1/me/tracks?limit=${limit}&offset=${offset}`, {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      });

      if (!response.ok) {
        throw new Error(`HTTP error! Status: ${response.status}`);
      }

      data = await response.json();
      allSongs = [...allSongs, ...data.items];
      offset += limit;
    } while (offset < data.total);

  } catch (error) {
    console.error('Error fetching songs:', error);
    // Handle error (e.g., show an error message to the user)
    return []; // Return an empty array or handle error accordingly
  }

  return allSongs.map((song) => ({
    track: song.track,
    compared: false,
    wins: 0,
  }));
};
